 /* eslint-disable */ 
<template>
	
	<div class="longo-page">
		<div class="register">
			<div class="wrp">
				<div class="layout">
					<div class="mi-bg"></div>
					<div class="fonts">
						<h1>注册小米帐号</h1>
					</div>
					<form @submit.prevent="submit">
						<div class="regbox">
							<div  class="region">
								<div class="wrate">
									<img src="@/assets/img/user.png"/>
									<label class="dq" for="text">用户名</label>
								    <input type="text" 
								    	   id="text" 
								    	   placeholder="请设置用户名"
								    	   name="name"
								    	   v-model.trim="form.name"/> 
								</div>							
							</div>
							<p class="entry">{{ entry.name }}</p>
							<div class="phoneNum">
								<img src="@/assets/img/password.png"/>
								<label class="dq" for="pas">密码</label>
								<input type="password" 
									   id="pas" 
									   placeholder="请设置登陆密码"
									   name="password"  
									   v-model.trim="form.password"/>
							</div>
							<p class="entry">{{ entry.pas }}</p>
							<div class="now">
								<input type="submit" value="立即注册"/>
							</div>
						</div>
					</form>
					<div class="treatybox">
						<input type="checkbox" id="check"/>
						<label for="check">
							<span>
								注册帐号即表示您同意并愿意遵守小米 
								<a href="https://static.account.xiaomi.com/html/agreement/account/cn.html" target="_blank">用户协议</a>和
							    <a href="https://www.mi.com/about/privacy/" target="_blank"> 隐私政策</a>
							</span>
						</label>
					</div>
				</div>
			</div>
		</div>
		<div class="register-footer">
			<div class="clearfix">
				<ul class="select-list">
					<li><a href="#">简体</a></li>
					<li><a href="#">繁体</a></li>
					<li><a href="#">English</a></li>
					<li><a href="#">常见问题</a></li>
				</ul>
			</div>
			<p>
				<span>
				小米公司版权所有-京ICP备10046444-
			   </span>
			   <a href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=11010802020134">京公网安备11010802020134号-京ICP证110507号</a>
			</p>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				form:{
				  name:'',
				  password:''
				},
				entry:{
					name:'',
					pas:""
				}
				
			}
		},
		methods:{
			submit(){
				if(!this.form.name){
					this.entry.name = '请设置登陆用户名';
				}
				if(!this.form.password){
					this.entry.pas = '请设置登陆密码';
				}
				if(this.form){
					this.$store.dispatch("submission",this.form);
				}
			}
		}
	}
	
	
</script>

