 /* eslint-disable */
import Vue from 'vue'
import Vuex from 'vuex'
import axios from '@/api/axios.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
     loggedIn:false,
     info:''
  },
  getters:{
  	
  },
  mutations:{
     logged(state,data){
     	 let res = data.data.data.name;
		   localStorage.setItem("user",JSON.stringify({loggedIn : true,info : JSON.stringify(res)}))
	   },
	   landed(state,res){
	   	  let name = res.data.data.name;
	   	  let user = JSON.parse(localStorage.getItem("user"));
	   	  let stateName = JSON.parse(user.info);
	   	  if(stateName == name){
	   	  	 state.loggedIn = true;
		       state.info = name;
	   	  }
	   }
  },
  actions:{
     submission({commit},form){
     	 axios.post("/register",form).then((res)=>{
     	 	 commit("logged",res)
     	 })
     },
     landing({commit},land){
     	axios.post("/login",land).then((res)=>{
     	 	 commit("landed",res)
     	 })
     }
  }
})
