				 	<img src="@/assets/img/mistore_logo.png" alt="" />
				 </a>
			</div>
		    <div class="m-link">
		    	<div class="bg-link">
		    		<a href="https://www.mi.com/mi9/" target="_blank"></a>
		    	</div>
