		    		<div class="fashion">
		    			<h2>账号登陆</h2>
		    		</div>
		    		<div class="box">
		    			<div class="login_area">
		    				
		    				<p>{{ formError }}</p>
		    				<form @submit.prevent="submit">
								<div class="reg">
									 <input type="text" 
									    	id="text" 
									    	placeholder="请输入用户名"
									    	name="name"
									    	v-model="land.name"/> 
								</div>
								<p class="entry">{{ entry.name }}</p>
								<div class="phones">
									<input type="password" 
										   id="pas" 
										   placeholder="请输入密码"
										   name="password"
										   v-model="land.password"/>
								</div>
								<p class="entry">{{ entry.pas }}</p>
								<div class="nows">
									<input type="submit" value="登陆"/>
								</div>
						    </form>
						    <p class="back">
						    	<router-link to="/logon">立即注册</router-link>
						    	 |
						    	<a href="#">忘记密码？</a>
						    </p>
		    			</div>
					</div>
		    	    <div class="other_login_type">
		    	    	<fieldset class="oth_type_tit">
		    	    		<legend align="center">其他方式登陆</legend>
		    	    	</fieldset>
		    	    	<div class="sns-login-links">
