				<ul class="select-list">
					<li><a href="#">简体</a></li>
					<li><a href="#">繁体</a></li>
					<li><a href="#">English</a></li>
					<li><a href="#">常见问题</a></li>
				</ul>
			</div>
			<p>
				<span>
				小米公司版权所有-京ICP备10046444-
			   </span>
			   <a href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=11010802020134">京公网安备11010802020134号-京ICP证110507号</a>
			</p>
		</div>
