	</div>
</template>

<script>
	export default{
		data(){
			return{
				land:{
					name:'',
					password:''
				},
				entry:{
					name:'',
					pas:''
				},
				formError:''
			}
		},
		methods:{
			submit(){
				if(!this.land.name){
					this.entry.name ='请输入用户名';
				}
				if(!this.land.password){
					this.entry.pas = '请输入密码';
				}
				if(this.land){
					this.$store.dispatch("landing",this.land)
					.then((res)=>{
						console.log('登陆成功')
						this.$router.replace({ path:'/' })
					}).catch((e)=>{
						this.formError = e.message
					})
				}
			}
		}
	}
	
	
</script>

